<template>
  <div class="QA">
    <van-collapse v-model="activeName" accordion @change="collapseChange">
      <van-collapse-item title="1.注册香港公司的风险有多大？" name="1">A:注册香港公司风险一：香港公司每周年需要年审。如不按时年审，更换新年度商业登记证，香港税务局将在年审到期42天追缴罚款，具体罚款数目请看香港公司年审费用介绍。<br/>二：香港公司不可租用写字楼在大陆挂牌经营，因为内地工商局只承认内地公司的企业法人营业执照。如在内地挂牌经营工商部门有权查封。<br/>三：新成立的香港18个月后需要报税，不管有无盈利都需要报税，请切记注册日期后推算18个月后为报税期限。否则产生不必要的麻烦与罚款。</van-collapse-item>
      <van-collapse-item title="2.注册新加坡公司时对名字有要求吗？" name="2">A:新加坡公司必须是英文名称，而且公司名称必须以英文Pte Ltd结尾。新加坡公司法规定设立公司注册资本一般是新币10万元，缴足资本则是新币2元起。股东可随时决定提高注册资本和缴足资本，缴足注册资本是公司成立后开了银行账户注入资金即可。</van-collapse-item>
      <van-collapse-item title="3.香港公司注册后想要注销会收费吗？" name="3">A:香港公司如果不需要了就可以办理注销，而且注销需要在年审前三个月之前进行，如果放任不管，会留下不良记录，并且香港公司会产生罚款，香港公司的股东董事信息也会进入到香港政府的黑名单中，对股东董事信息有不良影响。<br/>香港公司注销程序：<br/>1、申请人向税务局申请{不反对撤销注册通知书}<br/>2、税务局长签发｛不反对撤销注册通知书}记费<br/>3、公司必须向公司注册处提交申请撤销及相关费用<br/>4、香港公司注册处登宪报撤消注册</van-collapse-item>
      <van-collapse-item title="4.离岸公司和内地公司应该如何辨别有没有关联交易？" name="4">A:一般可以依以下标准判断离岸公司和内地公司是否存在关联交易：（1）外商投资企业或外国企业与其关联企业之间的业务往来， 应当按照独立企业之间的业务往来（即没有关联关系的企业之间， 按照公平成交价格和营业常规所进行的业务往）来收取或支付价款、费用。（2）对购销业务转让定价的检查：通过调查取证，发现被查企业与其关联企业的购销业务不按独立企业之间的业务往来作价的，可依照下列次序和确定的方法进行调整：可比价格法；转售价格法；成本加成法；其他合理的方法（核定利润率）。（3）对支付费用的检查应注意：外商投资企业不得列支向其关联企业支付的管理费；外国企业在中国境内设立机构向其总机构支付的与生产经营有关的合理费用应提交证明文件及注册会计师的查账报告，经当地税务机关审核同意后可列支。</van-collapse-item>
      <van-collapse-item title="5.在国外取得的商标注册证书要想在国内被认可该怎么办？如何办理，费用是多少？" name="5">A:国外的商标证书要想在国内被认可，就需要办理国外商标证书公证认证手续。办理时，首先由商标签发所在国公证人在查询后出具公证书，进而由该国外交部门或其授权部门认证，最后送中国驻该国使领馆认证。如果中国在该国没有使领馆，则需送到与该国有关系的第三国的中国使领馆办理认证。通常商标国外注册公证认证的时间为3周左右，办理费用根据不同国家而不同。</van-collapse-item>
      <van-collapse-item title="6.香港公司注册后在什么情况下不适合零报税？" name="6">A:香港公司报税可以分为零申报和做账核数报税，其中做账核数报税又可以分为代收代付申报和实账申报。在香港，没有经营的香港公司可以直接零报税，有经营的公司就必须做账、核数才能报税。 只要以公司名义进行商业上的交易或活动， 已经开始经营业务。 要是从书面记录来讲，可以由以下几个方面来判断，符合其中一方面的即为有经营业务：<br/>1、银行账户已经留下经营记录;<br/>2、政府海关、物流公司已经留下进出口记录;<br/>3、与香港客商发生购销关系;<br/>4、在香港已经聘请雇员;<br/>5、容许或授权在香港使用专利，商标设计等资料;<br/>6、容许或授权在香港使用动产收取租金，租赁费等款项;<br/>7、委托在香港代销;<br/>8、其它得自或产生于香港的利润</van-collapse-item>
      <van-collapse-item title="7.注册香港公司都需要注意哪些事项？" name="7">A:一、注册香港公司资料：<br/>1、香港公司注册证书：此证书相当于国内的营业执照，上面只会显示公司的编号，公司的中英文名字，以及公司的成立时间，法人与注册资金这上面是没有的。<br/>2、商业登记证：此证书相当于国内的税务登记证。政府每年都会有一些调整的。上面显示的内容是公司的中英文名字，注册地址，商业登记证的生效日期与界满日期，商业登记证号码，以及商业登记证的费用。看这张证书是否交过费用，看最下面是否有政府打印的条码。<br/>3、印章：一般是三枚的，长方形的签名章，圆形章，钢印。开户时，一般使用的是长方形的签名章。<br/>二、公司名称方面：<br/>1、公司取名名字不宜太长，一般银行都有字数限制，比如汇丰银行有30个字母的限制，超过三十个字，客户打款时容易由于名字输不完整，被退票。<br/>2、公司名字可以中英文或者只用英文名称，一个中文名字里不能含有英文字母。也不能英文名字里有中文汉字，政府规定不行。公司名称结尾中文必需是：“有限公司”，英文必需是：“LIMITED”OR "CO., LIMITED”。<br/>三、注册资金方面：<br/>1、从2012年的6月1号开始，香港政府正式取消了注册印花税千分之一的收取，不管注册多大注册资金都不用再另外交印花税费用。<br/>2、在配股这一块可以这样操作注册一百万，分一百万股，只配出一万，这样方便日后客户转股不用交千分之二的印花税。但如果注册资本过高，在银行开户时，可能会遇到一些麻烦，所以请谨慎选择。<br/>三、注册时间：<br/>新成立的公司，一般是8个工作日完成，如果购买现成公司，三个工作日就可以完成。四、法定文件签名：注册时，代理公司会做法定文件（NC1文件）传给你，并要求你打印出来签名，然后再快递原件给代理公司，在香港，签字是很重要的。</van-collapse-item>
      <van-collapse-item title="8.注册美国公司的几种结尾各有什么优缺点?" name="8">A:美国公司LLC结尾是Limited Liability Company 的缩写，叫做有限责任公司。LLP 是Limited Liability Partnership的缩写，叫做有限责任合伙公司。Inc是Incorporation的缩写，（结合, 合并, 形成法人组织, 组成公司(或社团) 。C-corporation –根据收入需付联邦及州税。当利润交给股东，股东申报个人税时再次需付收入税。双重克税为其最大缺点。C-corporation是任何一个国家的人都可以注册的，美国国籍的限制。S-corporation -法律特色如同C-Corporation，但不需直接付税，只要申报收入付个人税即可。S corporation需另符合下列几项条件: 拥有人（股东）少于100人；拥有人（股东）不可是非永久居民外国人；拥有人（股东）不可是其他法定公司、有限责任公司、合伙公司或信托。有限责任公司LLC是一个独立法人，介于合伙公司和法定公司，拥有法定公司的有限责任，和个人或合伙公司的税法。有限责任公司的拥有人称为成员。<br/>优点: 如同合伙公司，有限责任公司有营运协议。有限责任公司可由一位或更多成员组成。因是一个独立法人，有限合伙公司可拥有财产、告人、或被告。<br/>缺点: 因是一个独立法人，有限责任公司比一般合伙公司或个人公司需更多法律文件。 有限责任合伙公司LLP是一个独立法人，可保护所有一般合伙人。</van-collapse-item>
      <van-collapse-item title="9.注册BVI公司后如果不使用还用做年检吗?" name="9">A:在BVI注册成立公司进行离岸管理，无须向BVI政府交纳税金，深受国际投资者的青睐。所以近年来越来越多的国内客户选择注册BVI公司，但注册BVI公司每年必须要向当地政府办理续牌手续。BVI公司年审服务内容：<br/>1、续一年公司注册代理人存档费用；<br/>2、续一年公司注册地址费用；<br/>3、国际商业公司的牌照费；BVI公司的年检时间并不如其它离岸公司一样是对年的，所以很多客户不太确定自己的公司是在什么时间需要进行年检。<br/>BVI公司年检时间：<br/>1、1-6月份成立的BVI公司，公司执照年度费用应在每年的05月01日前提交续牌；<br/>2、7-12月份成立的BVI公司，公司执照年度费用应在每年的11月01日前提交续牌；<br/>3、未能如期缴付执照费的公司须缴交罚款，逾期五个月仍未付款的公司将被取消；<br/>4、BVI公司被公司注册处除名后，可向公司注册处申请恢复；申请恢复被除名的费用因为被除名的时间长短不同收费不同。</van-collapse-item>
      <van-collapse-item title="10.注册英国公司的性价比高还是离岸公司？" name="10">A:欧盟国家中，英国公司注册手续最简单，注册资本无需到位；若不在英国当地营业，无需交税属于合法；每年维护成本较低；税务安排便利；可在香港和英国开立银行帐户，可以以公司名义买卖物业；正常情况下，所有进入英国的货物可以在24小时内通关；可与世界各地主要金融市场和香港上市，依托“日不落帝国”盛名，在英国注册公司是树立国际形象、开拓国际市场的明智之举。注册英国公司的好处：<br/>1、注册时间，一般5个工作日；<br/>2、注册英国公司只需提供股东及董事的身份证或护照的复印件，股东/董事填写注册申请表即可；<br/>3、注册英国公司灵活性大，取名比较自由。股东可以是一名或者多名，国籍不限。另外，注册资本没有限制，标准注册资本为100英镑，不需验资；<br/>4、英国公司成立，查询方便，一般在注册完成后的24小时内就可以在英国工商局网站上查到注册信息；<br/>5、合法避税，一定条件下无须缴纳任何税款。</van-collapse-item>
      <van-collapse-item title="11.以个人名义可以注册香港离岸账户吗？" name="11">A:离岸账户是对于离岸公司而言而不是对个人而言，内地居民无法以个人身法办理离岸账户，必须成立离岸公司后，以公司名义方能开设离岸账户。成立离岸帐户，方便对外贸易操作，增加贸易商的信任。如果你是中国居民，那你想要开离岸帐户就只能是先注册一个离岸公司(比如香港公司)，然后拿着公司文件和资料等去银行开户。<br/>个人也可以在香港开银行帐户，但是这种帐户在公司贸易往来对公对私转帐是有所限制的。</van-collapse-item>
      <van-collapse-item title="12.注册英国公司和注册香港公司有什么区别？ " name="12">A:目前香港公司的注册价格低于英国公司，但是后续管理的话，香港公司比英国公司麻烦点，因为它需要年检和报税，而英国公司只需要周年交纳一个固定的政府牌照费。公司性质、注册资本要求、后续管理以及税务方面也有区别。香港公司管理规范，而在英国和其他岛屿国家注册离岸公司管理简单，维护成本低，不需要做审计，在国家贸易上拥有不同的优势。英国公司每年的后续管理要比香港公司简单，二者都是沿用英美法系，相比于岛屿公司而言呢，英国公司和香港公司都是永久性的公司，不用的话，需要注销！而且股东和董事的资料是可以查询到的！所以建议：如果单纯的为了出口收汇的话，香港公司和英国公司都可以。</van-collapse-item>
